.ant-layout-header {
  position: sticky;
  top: 0;
  z-index: 1;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 0;
  
  .logo-link-container {
    max-width: 180px;
    min-width: 180px;
    width: 180px;
    padding-left: 15px;
    
    .logo-image-animated {
      height: 30px;
      width: 30px;
    }
    
    .ant-typography {
      font-size: 25px;
    }
  }
  
  .ant-flex {
    display: flex;
    flex: 1 1;
    padding: 0 20px 0 6px;
  }
  
  .ant-breadcrumb {
    .ant-breadcrumb-link {
      display: flex;
      align-items: center;
      
      .header-icon {
        margin-right: 8px;
        
        & svg {
          width: 16px;
          height: 16px;
        }
      }
      
      .header-path {
        font-size: 16px;
        font-weight: bold;
      }
    }
  }
  
  .header-content {
    display: flex;
    align-items: center;
    gap: 16px;
    font-size: 14px;
  }
}
